<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-subheading>Choose Event Size</v-toolbar-subheading>
      <v-menu>
        <v-btn slot="activator" color="primary" dark>
          Events
        </v-btn>
        <v-list>
          <v-list-tile :items="eventName" v-for="(item, index) in eventName" :key="index" @click="() => switchData(item.title)">
          <v-list-tile-body-2>{{ item.title }}</v-list-tile-body-2>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>
      
    <v-toolbar flat color="white">
      <v-toolbar-subheading>Choose Energy Intensity</v-toolbar-subheading>
      <v-menu>
        <v-btn slot="activator" color="primary" dark>
          Energy Intensity
        </v-btn>
        <v-list>
          <v-list-tile :items="intensity" v-for="(item, index) in intensity" :key="index" @click="">
          <v-list-tile-body-2>{{ item.title }}</v-list-tile-body-2>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>
      
      
      
    <v-data-table
      :headers="headers"
      :items="events"
      :expand="expand"
      item-key="name"
    >
      <template slot="items" slot-scope="props">
        <tr>
          <td>{{ props.item.name }}</td>
          <td class="text-xs-right">{{ props.item.demand }}</td>
          <td class="text-xs-right">{{ props.item.lighting }}</td>
          <td class="text-xs-right">{{ props.item.plug }}</td>
          <td class="text-xs-right">{{ props.item.vertical }}</td>
          <td class="text-xs-right">{{ props.item.mechanical }}</td>
        </tr>
      </template>

    </v-data-table>
  </div>
</template>

<script>

    
  export default {
    data () {
      return {
        eventName: [
        { title: 'Small' },
        { title: 'Medium' },
        { title: 'Large' }
      ],
        intensity: [
        { title: 'Low' },
        { title: 'Moderate' },
        { title: 'High' }
      ],
        expand: false,
        headers: [
          {
            text: 'Floor',
            align: 'left',
            sortable: false,
            value: 'name'
          },
          { text: 'Total Demand (kwh)', value: 'demand' },
          { text: 'Lighting (kwh)', value: 'lighting' },
          { text: 'Plug Loads (kwh)', value: 'plug' },
          { text: 'Vertical Transportation (kwh)', value: 'vertical' },
          { text: 'Mechanical (kwh)', value: 'mechanical' }
        ],
        events: [
          {
            name: 'Basement',
            demand: 50,
            lighting: 10,
            plug: 20,
            vertical: 10,
            mechanical: 10
          },
          {
            name: '1st Floor',
            demand: 80,
            lighting: 20,
            plug: 10,
            vertical: 30,
            mechanical: 20
          },
          {
            name: '2nd Floor',
            demand: 100,
            lighting: 20,
            plug: 30,
            vertical: 30,
            mechanical: 20
          }
        ]
        }  
      },
    methods:{
      switchData(name){
        if (name=='Small'){
            this.events=[
          {
            name: 'Basement',
            demand: 50,
            lighting: 10,
            plug: 20,
            vertical: 10,
            mechanical: 10
          },
          {
            name: '1st Floor',
            demand: 80,
            lighting: 20,
            plug: 10,
            vertical: 30,
            mechanical: 20
          },
          {
            name: '2nd Floor',
            demand: 100,
            lighting: 20,
            plug: 30,
            vertical: 30,
            mechanical: 20
          }
        ]
        }
          else if (name=='Medium'){
              this.events=[
          {
            name: 'Basement',
            demand: 500,
            lighting: 100,
            plug: 200,
            vertical: 100,
            mechanical: 100
          },
          {
            name: '1st Floor',
            demand: 800,
            lighting: 200,
            plug: 100,
            vertical: 300,
            mechanical: 200
          },
          {
            name: '2nd Floor',
            demand: 1000,
            lighting: 200,
            plug: 300,
            vertical: 300,
            mechanical: 200
          }
        ]
          }
          else if (name=='Large'){
              this.events=[
          {
            name: 'Basement',
            demand: 5000,
            lighting: 1000,
            plug: 2000,
            vertical: 1000,
            mechanical: 1000
          },
          {
            name: '1st Floor',
            demand: 8000,
            lighting: 2000,
            plug: 1000,
            vertical: 3000,
            mechanical: 2000
          },
          {
            name: '2nd Floor',
            demand: 10000,
            lighting: 2000,
            plug: 3000,
            vertical: 3000,
            mechanical: 2000
          }
        ]
          }
        }
      }
  }
</script>

<style lang='stylus'>
</style>
