<template>
  <div>
    <v-layout row wrap pa-2 justify-center>
      <v-flex v-for='item in datalist' :key='item.name' xs12 lg3 class='pa-2'>
        <v-card class='mx-auto text-xs-center' color='#003057' dark max-width='600' height='250' hover>
          <v-card-text>
            <v-sheet color='white'>
              <v-sparkline :value='item.value' :labels='item.datelabels' color='#B3A369' height='80' padding='3' stroke-linecap='round' smooth :fill='true' line-width ='2' :show-labels='true'>
                <template slot='label' slot-scope='item' />
              </v-sparkline>
            </v-sheet>
          </v-card-text>

          <v-card-text>
            <div textcolor='#B3A369' class='headline font-weight-thin'>{{item.name}}</div>
          </v-card-text>
          <v-divider />
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  data: () => ({
    datalist: [{
      value: [423, 446, 675, 510, 590, 610, 760],
      name:'Solar Power',
      datelabels: ['-24hrs', '-20hrs', '-16hrs', '-12hrs', '-8hrs', '-4hrs', 'Now']
    }, {
      value: [825, 100, 800, 400, 300, 900, 1500],
      name:'Water Usage',
      datelabels: ['-24hrs', '-20hrs', '-16hrs', '-12hrs', '-8hrs', '-4hrs', 'Now']
    }, {
      value: [825, 100, 800, 400, 300, 900, 1500],
      name:'Power Usage',
      datelabels: ['-24hrs', '-20hrs', '-16hrs', '-12hrs', '-8hrs', '-4hrs', 'Now']
    }, {
      value: [825, 100, 800, 400, 300, 900, 1500],
      name:'Occupancy',
      datelabels: ['-24hrs', '-20hrs', '-16hrs', '-12hrs', '-8hrs', '-4hrs', 'Now']
    }]
  }),
  created () {
    const intervalFunction = () => {
      _.each(this.datalist, (n, i) => this.datalist[i].value = [..._.slice(n.value, 1), _.random(1500)])
      _.delay(intervalFunction, 5000)
    }

    intervalFunction()
  }
}
</script>

