import Vue from 'vue'
import Router from 'vue-router'
import Main   from './components/Main.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
    path: '/',
    name: 'main',
    component: Main
  }, {
    path: '/water',
    name: 'water',
    //** route level code-splitting
    //** this generates a separate chunk (about.[hash].js) for this route
    //** which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "kevin" */ './components/Water.vue')
  }, {
    path: '/energy',
    name: 'energy',
    //** route level code-splitting
    //** this generates a separate chunk (about.[hash].js) for this route
    //** which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "kevin" */ './components/Energy.vue')
  }, {
    path: '/jack',
    name: 'jack',
    //** route level code-splitting
    //** this generates a separate chunk (about.[hash].js) for this route
    //** which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ './components/Jack.vue')
  }, {
    path: '/matt',
    name: 'matt',
    //** route level code-splitting
    //** this generates a separate chunk (about.[hash].js) for this route
    //** which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ './components/Matt.vue')
  }, {
    path: '/xiaoshu',
    name: 'xiaoshu',
    //** route level code-splitting
    //** this generates a separate chunk (about.[hash].js) for this route
    //** which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ './components/Xiaoshu.vue')
  }]
})
